<template>
<div class="issueBox2">
    <div class="text">{{viewList.diseaseName}}</div>
    <div class="question">Q<span class="index">{{pageLevel}}</span>:{{viewList.diseaseDesc}}
    </div>
    <div class="btn">
        <div v-for="(item,i) in viewList.subDiseaseList" :key="i" 
        :class="isIndex == i?'active':''" @click="questionApi(item,i)">
        <i></i> 
        {{item.diseaseDesc}}
        </div>
    </div>
    <div class="blank"></div>
    <div class="intell-btn-box">
      <span class="fl renew-btn" @click="diseaseTap(0)">重新开始</span>
      <span class="fr" @click="diseaseTap(1)">下一步</span>
    </div>
</div>
</template>
<script>
  import Vue from "vue";
  import motheds from "@/api/motheds";
  import { mapMutations } from "vuex";
  import { mapGetters } from "vuex";
  export default {
    name: "Issue2",
    data() {
      return {
        viewList: {}, //传递给子组件的数据
        pageLevel:0,
        resultIssue:{},
        isIndex:null
      };
    },
    created() {},
    methods: {
       //获取测算页带过来的fromvalue值
      getInform() {
        this.$http.getHttp({
            name: "getInfo",
            params: [{ name: "key", val: this.$route.query.key }],
            header: 2
          },res => {
            var datas = JSON.parse(res.data);
            console.log(datas);
            sessionStorage.setItem("productProcess",JSON.stringify(datas.info.productProcess))
            this.writeInformDatas = JSON.parse(res.data).info;
          }
        );
      },
       /**疾病赋值 */
      questInit() {
        this.isIndex = null;
        this.resultIssue = {};
        //当前疾病等级
        this.pageLevel +=1;
        this.viewList = this.intell.issList[0];
        console.log(this.viewList)
      },
      /**问题参数过滤 */
      questionApi(item,i){
        this.isIndex = i
        //深拷贝当前问题数据 便于参数赋值
        if(_.isEmpty(this.resultIssue))this.resultIssue = _.cloneDeep(this.viewList);
        this.resultIssue.subDiseaseList = [item]
        console.log(this.resultIssue) 
      },
        /** 请求问题或结果*/
      intellAjax(datas){
        let load = this.$popup.loading("加载中...");
        this.$http.getHttp({
          name: "answerList",
          params: datas,
          code: this.intell.int_quey.code
        },
        res => {
          console.log(res)
          this.$popup.closeLay(load);
          if(res.data.questions){
            // 有问题时进入问题页
            this.intell.issList = res.data.questions;
            this.set_msg({key: "intelligent",lev: "issList",data: this.intell.issList}); //将当前选择项缓存
            this.questInit()
          }else{
            //无问题时进入结果页
            this.intell.results.success = res.data.success;
            this.intell.results.resultInfo = res.data.result;
            this.set_msg({key: "intelligent",lev: "results",data: this.intell.results}); //将当前选择项缓存
            this.$router.replace({path:'/result-2'}); //跳转到问题页
          }
        });
      },
      /**下一步与返回重选 */
       diseaseTap(next){
        if(next == 1){
          // 下一步
          if(_.isEmpty(this.resultIssue))return this.$popup.msg("请先选择问题答案")
          let datas = {
            questions:[this.resultIssue],
            paperId:this.intell.paperId,
            orderNo:this.intell.orderNo
          }
          this.intellAjax(datas)
        }else{
          // 返回重选
          this.$router.go(-1)
        }
      },
      goBack(next){
        var vm = this;
        this.$popup.inquiry("是否重新选择疾病",['重新开始','取消'],()=>{
          next()
        },()=>{
          next(false)
        })
      },
      ...mapMutations({
        set_msg: "SET_MESSAGE"
      })
    },
    computed: {
      // 使用对象展开运算符将 getter 混入 computed 对象中
      ...mapGetters([
        "intell",
        // ...
      ])
    },
    mounted() {
      this.questInit()
    },
    /**返回按钮显示 */
    beforeRouteLeave (to, from, next) {
      if (this.pageLevel>1 && to.name == 'Index2' && from.name == 'Issue2') {                 
        this.goBack(next)
        return
      }
      next()
    }
  };
</script>

<style lang="scss">
.issueBox2 {
  font-size: 16px;
  height: 100%;
  background-color: #fff;
  .text {
    display: inline-block;
    padding: r(30) 15px r(10);
    font-size: r(38);
    font-weight: bold;
    color: #000;
  }
  .question {
    padding: 15px;
    line-height: 1.5;
    font-size: r(36);
    font-weight: 900;
  }
  .btn {
    margin: r(25);
    color: #fff;
    div {
      padding: 10px;
      margin: 10px auto;
      width: 90%;
      color: #333;
      border:1px solid #dcdcdc;
      border-radius: 7px;
      box-sizing: content-box;
      i{
        display: inline-block;
        width: r(40);
        height: r(40);
        border-radius: 100%;
        vertical-align: middle;
        border: r(4) solid #dbdbdb;
        background-color: #fff;
        margin-right: r(10);
        position: relative;
        &::before{
          content: "";
          position: absolute;
          width: r(24);
          height: r(24);
          left: 50%;
          top: 50%;
          transform: translate(r(-12),r(-12));
          border-radius: 100%;
        }
      }
      &::before{
        content: "";
        display: inline-block;
        position: relative;
        height: 100%;
        width: 1px;
        left: 0;
        vertical-align: middle
      }
      &.active{
        background-color: rgba($color: $baseColor, $alpha: .2);
        border-color: $baseColor;
        box-shadow: 0 1px 5px rgba($color: $baseColor, $alpha: .5);
        i{
          border-color: $baseColor;
            &::before{
              background-color: $baseColor;
            }
        }
      }
    }
  }
  .intell-btn-box{
    background-color: #fff;
    border-top: 1px solid #d2d2d2;
    padding: r(20) r(30);
    width: 100%;
    height: r(130);
    position: fixed;
    left: 0;
    bottom: 0;
    span{
      display: block;
      width: r(300);
      height: r(90);
      line-height: r(90);
      font-size: r(38);
      color: #fff;
      text-align: center;
      background-color: $baseColor;
      border-radius: 5px;
    }
    .renew-btn{
      background-color: #fff;
      border: 1px solid $baseColor;
      color: $baseColor
    }
  }
}
</style>


